body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.auto-complete-wrap {
  position: relative;
}

.auto-complete-wrap-up {
  z-index: 99;
}

.auto-complete-wrap .complete-wrap,
.auto-complete-wrap .has-error-wrap {
  display: block;
  position: absolute;
  margin-top: -10px;
  margin-left: 10px;
}

.auto-complete-wrap .complete-none,
.auto-complete-wrap .has-error-none {
  display: none;
}

.auto-complete-wrap .input-item {
  width: 150px;
  height: 32px;
  line-height: 32px;
  padding: 0 10px;
  border: 1px solid #DDD;
  border-radius: 5px;
  margin: 0 10px;
  color: #888;
  font-size: 14px;
  font-weight: bold;
}

.auto-complete-wrap .suggestion-wrap {
  list-style: none;
  width: 200px;
  font-size: 12px;
  background: #FFF;
  border: 1px solid #EEE;
  border-radius: 5px;
  padding: 0;
  max-height: 240px;
  overflow-y: scroll;
}

.suggestion-item {
  color: #999;
  line-height: 36px;
  cursor: pointer;
  padding-left: 20px;
}

.suggestion-item:hover {
  background: #FCFCFC;
  color: #666;
  font-weight: bold;
}

.auto-complete-wrap .has-error-wrap {
  color: #F12F2F;
  font-size: 11px;
  margin-top: 10px;
}
.wrap {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #A6CB52;
}

.welcome {
  color: #FFF;
  margin-bottom: 80px;
  font-size: 40px;
  margin-top: 60px;
}

.input-wrap {
  font-size: 22px;
  color: #FFF;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.separator {
  display: inline-block;
  width: 150px;
  border-bottom: 1px solid #FFF;
  margin: 0 10px;
}

.search {
  display: inline-block;
  width: 180px;
  line-height: 32px;
  text-align: center;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  color: #FFF;
  margin-top: 50px;
  border: none;
  background: #5CA5F7;
  cursor: pointer;
  -webkit-transition: background 0.5s;
  transition: background 0.5s;
}

.search:hover {
  background: #0092ff;;
}

.search:disabled {
  background: #DDDDDD;
  color: #AAA;
  cursor: not-allowed;
}

.result-wrap {
  color: #FFF;
  font-size: 14px;
  line-height: 22px;
}

.search-wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.radio-wrap {
  margin-top: 20px;
  font-size: 13px;
  color: #FFF;
}

.radio-wrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.radio-wrap label {
  margin: 0 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.radio-wrap label input {
  padding: 0;
  margin: 2px 5px 0 0;
}

@media screen and (max-width: 900px) {
  .welcome {
    font-size: 26px;
    padding: 0 20px;
    margin-bottom: 30px;
  }

  .input-wrap {
    flex-direction: column;
  }

  .separator {
    display: inline-block;
    width: 1px;
    height: 30px;
    border-bottom: 0;
    margin: 10px;
  }

  .input-wrap .label {
    margin: 10px;
  }
}
